import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/index',
      name: 'index',
      components: require('@/views//Index/index.vue'),
    },
    {
      path: '',
      redirect: '/index',
    },
  ],
})
