





























import { Component, Vue } from 'vue-property-decorator'
import PhotoWinow from './components/photoWinow.vue'

@Component({
  components: {
    PhotoWinow,
  },
})
export default class Index extends Vue {
  private action = 'one'
  private isOff: boolean = true

  public mounted() {
    // 自动播放音乐效果，解决微信自动播放问题
    document.addEventListener('touchstart', this.audioAutoPlay, false)
    document.addEventListener('WeixinJSBridgeReady', this.audioAutoPlay, false)
    const oAudio: any = document.querySelector('#audio')
    oAudio.onended = () => { // 播放完毕，重新循环播放
      oAudio.load()
      oAudio.play()
    }
  }

  private changeOn() {
    const oAudio: any = document.querySelector('#audio')
    if (this.isOff) {
      oAudio.play()// 开始播放
    } else {
      oAudio.pause()// 暂停播放
    }
    this.isOff = !this.isOff
  }

  private audioAutoPlay() {
    console.log(111)
    const audio: any = document.getElementById('audio')
    this.isOff = false
    audio.play()
    document.removeEventListener('touchstart', this.audioAutoPlay)
  }

  private changeAction(val: string) {
    this.action = val
  }
}
