



























import { Component, Vue, Emit, Prop } from 'vue-property-decorator'

@Component({})
export default class PhotoWinow extends Vue {
  private isShow: boolean = false
  private isShow1: boolean = false
  private isBig: boolean = false
  private isGo: boolean = false
  private isRotate: boolean = false
  private hackReset!: boolean
  private indexImg: number = 1
  private imgOne: number = 2
  private imgTwo: number = 3
  private imgThree: number = 4
  private imgFour: number = 5
  private imgArr: number[] = [1, 2, 3, 4, 5, 6, 7, 8, 9]

  @Prop(String)
  private action!: string

  public mounted() {
    this.loop()
  }

  private loop() {
    this.isShow1 = false
    this.isShow = false
    this.isBig = false
    this.isGo = false
    this.isRotate = false
    setTimeout(() => {
      this.isShow = !this.isShow
      setTimeout(() => {
        this.isBig = !this.isBig
        this.isRotate = !this.isRotate
        setTimeout(() => {
          this.isGo = !this.isGo
          setTimeout(() => {
            if (this.indexImg === 9) {
              this.indexImg = 1
            } else {
              this.indexImg++
            }
            this.randomNumber()
            this.loop()
          }, 1000)
        }, 3000)
      }, 2000)
    }, 1000)
  }

  private randomNumber() {
    const imgOne = Math.floor(Math.random() * 9 + 1)
    const imgTwo = Math.floor(Math.random() * 9 + 1)
    const imgThree = Math.floor(Math.random() * 9 + 1)
    const imgFour = Math.floor(Math.random() * 9 + 1)
    const arr = [imgOne, imgTwo, imgThree, imgFour, this.indexImg]
    if ([...new Set(arr)].length === 5) {
      this.imgOne = imgOne
      this.imgTwo = imgTwo
      this.imgThree = imgThree
      this.imgFour = imgFour
      return
    } else {
      this.randomNumber()
    }
  }
}
